import React from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import RoomFilter from '../Filters/RoomFilter';
import CostFilter from '../Filters/CostFilter';
const hotelListForTrip ="https://developerfunnel.herokuapp.com/hotellist";


class ListingApi extends React.Component{
    constructor(){
        super();
        this.state={
            hotelsWithTrip:''
        }
    }
    displayFilterByRoom=(data)=>{
        this.setState({
            hotelsWithTrip:data
        })
    }
    displayFilterByCost=(data)=>{
        this.setState({
            hotelsWithTrip:data
        })
    }
    render(){
        return(
            <div className="row">
                <div className="col-md-2">
                    <RoomFilter filterByRoom={(data)=>this.displayFilterByRoom(data)} />
                    <CostFilter filterByCost={(data)=>this.displayFilterByCost(data)}/>
                    
                </div>
                <div className="col-md-10">
                    <ListingDisplay hotellist={this.state.hotelsWithTrip}/>
                </div>
            </div>
        )
    }
    componentDidMount=()=>{
        var tripId = this.props.match.params.id;
        sessionStorage.setItem('tripid',tripId);
        axios.get(`${hotelListForTrip}/${tripId}`)
        .then((res)=>this.setState({
            hotelsWithTrip:res.data
        }))
    }
}
export default ListingApi;