const BookingDisplay = ( props ) => {
    const renderTable = ({bookings}) => {
        if(bookings){
            return bookings.map((item)=>{
                return (
                    <tr>
                        <td>{item.id}</td>
                        <td>{item.hotelname}</td>
                        <td>{item.name}</td>
                        <td>{item.phone}</td>
                        <td>{item.status}</td>
                    </tr>
                )
            })
        }
    }
    return(
        <div className="contianer">
            <center><h3>Bookings</h3></center>
            <table className="table table-responsive">
                <thead>
                    <tr>
                        <th>Order Id</th>
                        <th>Hotel</th>
                        <th>Name</th>
                        <th>Phone</th>
                        <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable(props)}
                </tbody>

            </table>

        </div>
    )
}
export default BookingDisplay;