import axios from 'axios';
import React from 'react';
import BookingDisplay from './BookingDisplay';

const burl="http://localhost:8900/bookings"

class ViewBooking extends React.Component{
    constructor(){
        super()
        this.state={
            bookingDetails:""
        }
    }
    render(){
        return(
            <div className="container">
                <BookingDisplay bookings={this.state.bookingDetails}/>
            </div>
            
        )
    }
    async componentDidMount(){
        const response = await axios.get(burl);
        this.setState({bookingDetails:response.data})
    }
}
export default ViewBooking;