import React from 'react';
import axios from 'axios';
const curl="https://developerfunnel.herokuapp.com/hotellist";

var dict={
    1:{
        lval:2000,
        hval:4000
    },
    2:{
        lval:4000,
        hval:6000
    },
    3:{
        lval:6000,
        hval:8000
    },
    4:{
        lval:8000,
        hval:10000
    }
}


class CostFilter extends React.Component{

    filterByCost=(event)=>{
        const costID = event.target.value;
        const tripId = sessionStorage.getItem('tripid');
        let carFilterUrl,highValue,lowValue;
        if (costID==0){
            carFilterUrl=`${curl}/${tripId}`
        }else{
            highValue=dict[costID].hval;
            lowValue=dict[costID].lval;
            carFilterUrl=`${curl}/${tripId}?hcost=${highValue}&lcost=${lowValue}`
        }
        axios.get(carFilterUrl)
        .then((res)=>{
            this.props.filterByCost(res.data)
        })
        

    }
    render(){
        return(
            <React.Fragment>
                <center>Filter By Cost</center>
                <div onChange={this.filterByCost}>
                    <label className="radio">
                        <input type="radio" name="cost" value="0"/>All
                    </label>
                    <label className="radio">
                        <input type="radio" name="cost" value="1"/>2000-4000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cost" value="2"/>4000-6000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cost" value="3"/>6000-8000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cost" value="4"/>8000-10000
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default CostFilter;