import React from 'react';
import Slider from "react-slick";
import axios from 'axios';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import {Link} from 'react-router-dom';

const url="https://developerfunnel.herokuapp.com/hotelsdetails";

var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
};

class Display extends React.Component{
    constructor(){
        super();
        this.state={
            hotel:'',
            tripid:sessionStorage.getItem('tripid')
        }
    }


    render(){
        let {hotel}=this.state;
        sessionStorage.setItem('hotelname',hotel.name)
        return(
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <div className="row">
                                <div className="col-md-11">
                                    <h3>{hotel.name}</h3>
                                </div>
                                <div className="col-md-1">
                                    <Link to={`/list/${this.state.tripid}`} className="btn btn-danger">Back</Link>
                                </div>
                            </div>
                        </div>
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <Slider {...settings}>
                                        <div>
                                            <img src={hotel.thumb} className="img-responsive" style={{width:1500,height:400}}/>
                                        </div>
                                        <div>
                                            <img src="https://i.ibb.co/5nKNcz1/pride.jpg" className="img-responsive" style={{width:1500,height:400}}/>
                                        </div>
                                        <div>
                                            <img src="https://i.ibb.co/QD1RRBk/piccadily.jpg" className="img-responsive" style={{width:1500,height:400}}/>
                                        </div>
                                    </Slider>
                                </div>
                            </div>
                            <h1>{hotel.name}</h1>
                            <div>
                                <Tabs>
                                    <TabList>
                                        <Tab>Address</Tab>
                                        <Tab>Cost</Tab>
                                        <Tab>Amenities</Tab>
                                    </TabList>

                                    <TabPanel>
                                        <h4>{hotel.locality}</h4>
                                        <h4>{hotel.address}</h4>
                                    </TabPanel>
                                    <TabPanel>
                                        <h4>&#8377;{hotel.cost}</h4>
                                    </TabPanel>
                                    <TabPanel>
                                        <h3>
                                            <span class="glyphicon glyphicon-road"></span>Parking <br/>
                                            <span class="glyphicon glyphicon-fire"></span>Bone Fire
                                        </h3>
                                    </TabPanel>
                                </Tabs>
                            </div>
                            <Link to={`/booking/${hotel._id}`} className="btn btn-success">PlaceBooking</Link>
                        </div>
                    </div>
                </div>
            )
        }

    async componentDidMount(){
        var hotelid = this.props.match.params.id;
        const response = await axios.get(`${url}/${hotelid}`)
        this.setState({hotel:response.data[0]})
    }
}

export default Display;
