import React from 'react';
import {Link} from 'react-router-dom';
const QuickSearch = (props) =>{
    const displayBusinessTitles=({businessValue})=>{
        if(businessValue){
            return businessValue.map((item)=>{
                return(
                    <Link to={`/list/${item.trip}`}>
                        <div className="tileContainer">
                            <div className="tileComponent1">
                                <img src={item.image}/>
                            </div>
                            <div className="tileComponent2">
                                <div className="componentHeading">
                                    {item.name} 
                                </div>
                                <div className="componentSubHeading">
                                welcome to our { item.name }  Hotels
                                </div>
                            </div>
                        </div>
                    </Link>
                )
            })
        }
    }
    return(
        <div className="quickSearchContainer">
            <div className="quickSearchHeading">
                Recommendations
            </div>
            <div className="quickSearchSubHeading">
                Come here for your dream destinations
            </div>
            <br/>
            {displayBusinessTitles(props)}
        </div>
    )
}
export default QuickSearch;