import React from 'react';
import axios from 'axios';
const rurl="https://developerfunnel.herokuapp.com/hotellist";
class RoomFilter extends React.Component{
    filterHotelByRoom=(event)=>{
        var roomId = event.target.value;
        var tripId= sessionStorage.getItem('tripid');
        var roomUrl;
        if (roomId==""){
            roomUrl=`${rurl}/${tripId}`
        }else{
            roomUrl=`${rurl}/${tripId}?roomtype=${roomId}`
        }
        axios.get(roomUrl)
        .then((res)=>{
            this.props.filterByRoom(res.data)
        })
    }
    render(){
        return(
            <React.Fragment>
                <center>Room Filter</center>
                <div onChange={this.filterHotelByRoom}>
                    <label className="radio">
                        <input type="radio" value=" " name="room"/>All
                    </label>
                    <label className="radio">
                        <input type="radio" value="1" name="room"/>Deluxe Room
                    </label>
                    <label className="radio">
                        <input type="radio" value="2"name="room" />Premium Room
                    </label>
                    <label className="radio">
                        <input type="radio" value="3" name="room"/>Travel Room
                    </label>
                    <label className="radio">
                        <input type="radio" value="4" name="room"/>Semi Deluxe Room
                    </label>
                    <label className="radio">
                        <input type="radio" value="5" name="room"/>Good Room
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default RoomFilter;