import React from 'react';
// first find the city
// second find the hotel respect to the city
import './Search.css';
import {withRouter} from 'react-router-dom';

const cityurl ="https://developerfunnel.herokuapp.com/location";
const hotelurl = "https://developerfunnel.herokuapp.com/hotels?city=";
class Search extends React.Component{
    constructor(){
        super();
        this.state={
            cityName:'',
            cityHotels:''
        }
    }
    
    renderCity=(data)=>{
        if(data){
            return data.map((item)=>{
                return(
                    <option value={item.city}>
                        {item.city_name}
                        </option>
                    )
                })  
            }
    }
    
    hotelAccordingToCity=( event )=>{
        const cityid = event.target.value
        fetch(`${hotelurl}${cityid}`,{method:"Get"})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({
                cityHotels:data
            })
        })
        .catch((err)=>{
            console.log('error',err)
        })
    }

    handleHotel=(event)=>{
       this.props.history.push(`/display/${event.target.value}`)
    }

    renderHotel=(data)=>{
        if(data){
            return  data.map((item)=>{
                return(
                    <option value={item._id}>
                        {item.name}|{item.locality}
                    </option>
                )
            })
        }
    }
    
  
    render(){
        console.log('search',this.props)
        return(
            <div className="imageContainer">
               <div id="logo">
                    A!
               </div>
               <div className="heading">
                   Plan trip with us
               </div>
               <div className="locationSelector">
                   <select className="locationDropDown" onChange={this.hotelAccordingToCity}>
                       <option>--------Select location-------------</option>
                       {this.renderCity(this.state.cityName)}
                   </select>
                    
                   <select className="reataurantsinput" onChange={this.handleHotel}>
                       <option>--------Select Hotel-------------</option>
                       {this.renderHotel(this.state.cityHotels)}
                       
                   </select>
               </div>
            </div>
        );
    }

    componentDidMount(){
        fetch(cityurl,{method:'GET'})
        .then((res)=>res.json())
        .then((ans)=>{
            this.setState({
                cityName:ans
            })
        })
    }
   
  
}
export default withRouter(Search);